@import url(https://fonts.googleapis.com/css2?family=Luckiest+Guy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  font-family: 'Luckiest Guy', cursive;
  font-weight: lighter;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App{
  width: 100%;
  height: 100vh;
  color: #fff;
  background: linear-gradient(-45deg,#4776e6, #8e54e9, #6441A5);
  background-size: 400% 400%;
  position: relative;
  animation: change 4s ease-in-out infinite;
  text-align: center;
}
.App_game_name{
  font-size: 45px;
  padding-top: 120px;
  animation-name: floating;
  animation-duration: 3s; 
  animation-iteration-count: infinite; 
  animation-timing-function: ease-in-out;
}

.App_player{
  padding-top: 120px;
  animation-name: floating; 
  animation-duration: 4s; 
  animation-iteration-count: infinite; 
  animation-timing-function: ease-in-out; }

.App_player_name{
  color: #6441A5;
  font-size: 28px;
  width: 50%;
  height: 60px;
  border: #fff;
  border-radius: 30px;
  text-align: center;
  background-color: #fff;
}

.App_start{
  margin-top: 30px;
}
.App_player_name:focus{
  outline: none;
}
.App_start_btn{
  border-radius: 28px;
  margin: 0 auto;
  padding: 10px;  
  width: 140px;
  color: #6441A5;
  background-color: #fff;
  animation-name: floating; 
  animation-duration: 5s; 
  animation-iteration-count: infinite; 
  animation-timing-function: ease-in-out; 
  cursor: pointer;
  transition: 0.3s;
}
.App_start_btn:hover{
  color: #fff;
  background-color: #6441A5;
  border: #fff;
}


@keyframes change{
  0%{
    background-position: 0 50% ;
  }
  50%{
    background-position: 100% 50% ;
  }  
  100%{
    background-position: 0 50% ;
  }  

}

@keyframes floating { 
  0% { transform: translate(0,  0px); } 
  50%  { transform: translate(0, 15px); } 
  100%   { transform: translate(0, -0px); }     
} 
.game_details{
    display: flex;
    justify-content: space-between;
    padding: 50px 100px;
}
.game_play{
    width: 300px;
    height: 200px;
    width: 100%;
    padding: 100px 100px;
    display: flex;
    justify-content: space-between;
}
.game_controls_reset{
    position: absolute;
    bottom: 0;
    width: 100%;

}
.reset{
    margin: 30px;
    background-color: #6441A5;
    cursor: pointer;
}
.game_play_door_good{
    background-color: white;
    background-image: url(/DoorGame/static/media/god.74024abd.png);
    background-repeat: no-repeat;
    background-size: contain;
    position:relative;
    width:200px;
    height:300px;
    margin: 0 auto;
    margin-top:50px;
}

.game_play_door_bad{
    background-color: white;
    background-image: url(/DoorGame/static/media/devil.15522d89.png);
    background-repeat: no-repeat;
    background-size: contain;
    position:relative;
    width:200px;
    height:300px;
    margin: 0 auto;
    margin-top:50px;
}



img{  
    background-color: brown;
    position:absolute;
    top:0px;
    left:0px;
    width:200px;
    height:300px;
    transform-origin: left;
    /*Speed of the Door animation*/
    transition: all 0.5s ease-in-out;
}

.doorOpen
{
    left: 0;
  /*prespectiv creates the door open effect*/
  transform: perspective(1200px) translateZ(0px) translateX(0px) translateY(0px) rotateY(-105deg);
}


